.channel-head{
    display: flex;
    align-items: center;
    border-bottom: 1px solid #e5e2e2;
}
.channel-head img{
    height: 144px;
    width: 144px;
    border-radius: 50%;
    margin: 20px;
}
.channel-head span{
    font-size: 2rem;
    font-weight: 400;
    padding: 2px;
}
.channel-head p{
    font-size: 1.6rem;
    padding: 2px;
}
.channel-head button{
    border: none;
    height: 30px;
    width: 90px;
    border-radius: 60px;
    background-color: #f00;
    color: #fff;
    padding: 2px;
}
button{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.min-{
    width: 100%;
    text-align: center;
    font-size: 2.8rem;
    border-bottom: 1px solid #e5e2e2;
    padding-bottom: 25px;
}
.banner{
    width: 100%;
    height: 200px;
    background: linear-gradient(to top, #000, #ffffff00);
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}
.banner img{
    opacity: 60%;
}
.buttons{
    display: flex;
    gap: 2px;
}