.r-p{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5px;
    color: #ff0000;
    border: 1px solid #ff0000 ;
    padding: 50px;
    background-color: #ffffff00;
    transition: 0.5s;
    backdrop-filter: blur(10px);
}

.l-p{
    width: 100vw;
    height: 100vh;
    background-image: url('https://firebasestorage.googleapis.com/v0/b/sira-ai-api.appspot.com/o/hao-logo.png?alt=media&token=b987dabf-f5f7-47af-a21d-8dfddd33be2f');
    background-repeat: no-repeat;
    background-size: auto;
}
@media only screen and (max-width: 878px) {
    .l-p{
        display: flex;
        flex-direction: column;
    }
    .button{
        width: 100%;
    }
  }
.form-control{
    width: 100%;
    padding: 10px;
    border-radius: 50px;
    border: 1px dashed #ff0000;
}
.r-p form{
    text-align: center;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.r-p label{
    font-size: 1.4rem;
    padding: 10px;
    color: #ff0000;
}