.file-drop-area {
    position: relative;
    display: flex;
    align-items: center;
    width: 450px;
    max-width: 100%;
    margin-bottom: 5px;
    padding: 25px;
    border: 1px dashed rgb(255, 0, 0);
    border-radius: 5px;
    transition: 0.2s;
    &.is-active {
      background-color: rgb(63, 63, 63);
    }
  }
.dfr{
  background-image: url('https://firebasestorage.googleapis.com/v0/b/sira-ai-api.appspot.com/o/hao-logo.png?alt=media&token=b987dabf-f5f7-47af-a21d-8dfddd33be2f');
    background-repeat: no-repeat;
    background-size: auto;
    text-align: center;
}
.dfr form{
  backdrop-filter: blur(20px);
  height: 100vh;
}

.file-drop-area-v {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 5px;
    width: 450px;
    height: 225px;
    max-width: 100%;
    padding: 25px;
    border: 1px dashed rgb(255, 0, 0);
    border-radius: 5px;
    transition: 0.2s;
    &.is-active {
      background-color: rgb(63, 63, 63);
    }
  }
  
  .fake-btn {
    flex-shrink: 0;
    background-color: rgba(255, 255, 255, 0.04);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 3px;
    padding: 8px 15px;
    margin-right: 10px;
    font-size: 12px;
    text-transform: uppercase;
  }
  
  .file-msg {
    font-size: small;
    font-weight: 300;
    line-height: 1.6;
    white-space: nowrap;
    text-decoration: underline #ff0000;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  .file-input {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    cursor: pointer;
    opacity: 0;
    &:focus {
      outline: none;
    }
  }
  
  footer {
    margin-top: 50px;
    a {
      color: rgba(255, 255, 255, 0.4);
      font-weight: 300;
      font-size: 14px;
      text-decoration: none;
      &:hover {
        color: white;
      }
    }
  }

  .form__group {
    position: relative;
    padding: 15px 0 0;
    margin-top: 10px;
    width: 450px;
    margin-bottom: 5px;
  }
  
  .form__field {
    font-family: inherit;
    width: 100%;
    border: 1px dashed #ff0000;
    border-bottom: 2px solid #ff0000;
    border-radius: 5px;
    outline: 0;
    font-size: 1.3rem;
    color: #ff0000;
    padding: 7px 0;
    background: transparent;
    transition: border-color 0.2s;
  }
  .form__field::placeholder {
    color: transparent;
  }
  .form__field:placeholder-shown ~ .form__label {
    font-size: 1.3rem;
    cursor: text;
    top: 20px;
  }
  
  .form__label {
    position: absolute;
    top: 0;
    display: block;
    transition: 0.2s;
    font-size: 1rem;
    color: #9b9b9b;
  }
  
  .form__field:focus {
    padding-bottom: 6px;
    font-weight: 700;
    border-width: 3px;
    border-bottom: linear-gradient(to right, #ff1717, #ff0000);
    border-image-slice: 1;
  }
  .form__field:focus ~ .form__label {
    position: absolute;
    top: 0;
    display: block;
    transition: 0.2s;
    font-size: 1rem;
    color: #ff0000;
    font-weight: 700;
  }
  
  /* reset input */
  .form__field:required, .form__field:invalid {
    box-shadow: none;
  }
  .button {
    display: flex;
    align-items: center;
    background-color:#ff0000;
    color: #fff;
    border:none; 
    border-radius:10px; 
    padding:15px;
    width: 450px;

    transition: 0.25s;
  }
  .button:hover{
    background-color:#ffffff00;
    border: 1px dashed #ff0000;
    color: #ff0000;
  }