.v-c{
    flex-direction: column;
    flex-wrap: nowrap;
}
.v-c hr{
    width: 100%;
}
.v-c .video .video-title{
    font-size: 2.6rem;
    max-width: 100%;
    display: block;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-height:normal;
    line-clamp: 2;
    -webkit-box-orient: vertical;
}
.v-c .video{
    display: flex;
    justify-content: left;
    width: 100%;
    height: auto;
}
@media screen and (max-width: 1060px) {
    .v-c{
        flex-direction: row;
        flex-wrap: wrap;
    }
    .v-c .video{
        display: grid;
    }
    .v-c .video .video-title{
        font-size: 1.6rem;
    }
}
.v-c .video .thumbnail img{
    max-width: 360px;
}
.v-c .video .thumbnail{
    max-width: 360px;
}
.a-s{
    text-decoration: none;
    color: #000;
}
.a-s .channel-head:hover{
    background-color: #d1d1d1;
    border-radius: 10px;
}