#sidebar{
    background-color: #ffffff;
    width: 100px;
    height: 100%;
    border-right: 1px solid #eeeeee;
}
#sidebar nav{
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column; 
}
#sidebar .nav-link{
    display: flex;
    width: 90%;
    align-items: center;
    justify-content: center;
    padding: 5px;
    text-decoration: none;
    color: #000;
    border-radius: 4px;
    flex-direction: column;
    padding: 20px;
    margin: 10px;
}

.content{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: start;
    justify-content: center;
}
.main-part{
    max-width: calc(100vw - 628px);
}
.video-player video{
    width: calc(100vw - 628px);
    height: 480px;
    border-radius: 5px;
}
.video-info{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2px;
}
.video-channel{
    display: flex;
    align-items: center;
    text-decoration: none;
}
.video-channel img{
    width: 36px;
    height: 36px;
    margin-right: 5px;
}
.video-channel span{
    color: #000;
    font-size: 1.6rem;
    font-weight: 400;
    padding: 2px;
    display: -webkit-box;
    -webkit-line-clamp:1;
    line-clamp: 2;
    overflow: hidden;
    -webkit-box-orient: vertical;
}
.video-info button{
    background-color: #e7e7e7;
    display: flex;
    align-items: center;
    gap: 5px;
    padding: 8px;
    width: auto;
    border-radius: 50px;
    border: none;
}
.video-describtion{
    background-color: #e7e7e7;
    padding: 15px;
    border-radius: 20px;
    margin: 20px 0;
}
.video-describtion span{
    color:#3b3b3b;
}
.video-describtion p {
    margin-top: 10px;
    font-size: 1.2rem;
    overflow-x:auto;
}
.video-foryou {
    max-width: 628px;
    padding-left: 10px;
    overflow-y: auto;
}
.video-foryou .video{
    display: flex;
    width: 100%;
    margin-bottom: 0;
}
.video-foryou .video .thumbnail{
    width: 144px;
    height: 72px;
    margin-right: 10px;
}
.video-foryou .video .thumbnail img{
    width: 144px;
    height: 72px;
}
.channel-f{
    display: flex;
    align-items: center;
}
.channel-f img{
    height: 24px;
    width: 24px;
}
.cve{
    display: none;
}

@media screen and (max-width: 1267px) {
    #sidebar{
        display: none;
    }
    #sidebar nav{
        width: 100%;
        flex-direction: row; 
    }
    .content{
        flex-direction: column;
        justify-content: left;
        align-items: center;
        width: 100%;
        padding: 0;
    }
    .main-part{
        max-width: 100%;
    }
    .video-player video{
        width: 100%;
        height: 100%;
    }
    .video-player {
        border: 2px solid #000;
    }
    .cve{
        display: block;
    }
    .video-foryou {
        max-width: auto;
        padding-left: 0;
        overflow-y: visible;
    }
    .i-l{
        height: calc(100vh - 56px);
    }
  }

@media screen and (max-width: 477px) {
    #sidebar{
        display: block;
        position: fixed;
        z-index: 9999;
        width: 100%;
        height: 80px;
        bottom: 0;
    }
    .video-foryou{
       padding-bottom: 180px;
    }
}