@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Audiowide&family=Montserrat:ital,wght@0,100..900;1,100..900&family=Radio+Canada+Big:ital,wght@0,400..700;1,400..700&display=swap');
* {
    margin : 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 10px;
}

body {
    font-family: "Radio Canada Big", sans-serif;
}
header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 56px;
    padding: 0 16px;
}
.left-section{
    display: flex;
    align-items: center;
    cursor: pointer;
}
.left-section img{
    width: 80px;
    padding-left: 16px;
}
.mid-section{
    display: flex;
    align-items: center;
    margin-left: -9%;
}
.mid-section form{
    display: flex;
    height: 40px;
    width: 628px;
}
.mid-section input{
    width: 100%;
    border-radius: 20px 0 0 20px;
    padding: 12px;
    font-size: 1.6rem;
    border: 1px solid #d3d3d3;
    border-right: none;
}
.mid-section button{
    width: 64px;
    border-radius: 0 20px 20px 0;
    border: 1px solid #d3d3d3;
    padding-top: 4px;
    background-color: #f8f8f8;
    cursor: pointer;
}
.mid-section button:hover{
    border-color: #c6c6c6;
    background-color: #f0f0f0;
}
.right-section{
    display: flex;
    align-items: center;
    margin-right: 4px;
}
.right-section a {
    display: flex;
    align-items: center;
    padding: 18px;
    text-decoration: none;
    color: #000;
}
.right-section .account-img{
    height: 36px;
    width: 36px;
    border-radius: 50%;
    cursor: pointer;
}
.right-section a{
    padding: 5px;
    border-radius: 50%;
}
.right-section a:hover{
    background-color: #f8f8f8;
}
.content-area{
    height: calc(100vh - 56px);
    display: flex;
}
.content-area aside{
    height: 100%;
    width: 280px;
    overflow-y: auto;
}
.content-area nav{
    width: 100%;
    display: flex;
    flex-direction: column;
    margin: 15px 0;
    padding-right: 15px;
}
.nav-link{
    display: flex;
    align-items: center;
    padding: 10px 16px;
    text-decoration: none;
    color: #000;
    border-radius: 4px;
}
.nav-link:hover{
    background-color: rgba(0, 0, 0, 0.05);
}
.nav-link span{
    margin-left: 24px;
    font-size: 1.4rem;
    line-height: 2em;
    font-weight: 400;
    color: #030303;
}
.active{
    background-color: #e5e5e5;

}
#active-span{
    font-weight: 600;
}
hr{
    height: 1px;
    background-color: #e5e5e5;
    border: none;
    margin: 10px 0;
}
nav p {
    font-size: 1.4rem;
    color: #606060;
    font-weight: 500;
    padding: 8px 16px;
    text-transform: uppercase;
}
.channel-img{
    height: 24px;
    width: 24px;
    border-radius: 50%;
}
main{
    width: 100%;
    height: 100%;
    padding: 35px 15px;
    overflow-y: auto;
}
.videos-container{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
}
.video{
    width: 330px;
    margin-bottom: 40px;
    padding: 3px;
    cursor: pointer;
}
.video:hover{
    background-color: #d3d3d3;
    border-radius: 20px;
}
.thumbnail{
    width: 100%;
    height: 187px;
    margin-bottom: 12px;
}
.thumbnail img{
    height: 100%;
    width: 100%;
    border-radius: 15px;
}
.video-details{
    display: flex;
}
.creator-img img{
    height: 40px;
    width: 40px;
    border-radius: 50%;
    margin-right: 12px;
}
.title{
    display: flex;
    flex-direction: column;
}
.title a{
    text-decoration: none;
}
.video-title{
    color: rgba(3, 3, 3);
    font-size: 1.6rem;
    max-width: 278px;
    font-weight: 500px;
    overflow: hidden;
    line-height: 2.2rem;
    margin-bottom: 4px;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
}
.video-creator,.title span{
    color: rgba(96, 96, 96);
    font-size: 1.4rem;
    line-height: 2rem;
    font-weight: 400;
    display: -webkit-box;
    -webkit-line-clamp:1;
    line-clamp: 2;
    overflow: hidden;
    -webkit-box-orient: vertical;
}
.video-creator{
    color: #000;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #fff; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    width: 8px;
    border-radius: 100px;
    background: #ddd; 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #ccc; 
  }

  @media only screen and (max-width: 982px) {
    .mid-section input{
        width: 10%;
    }
    .mid-section{
        margin-left: 0%;
    }
    .mid-section form{
        width: 100px;
    }
  }
  .on{
    display: none;
  }
  .clo{
    border-radius: 50%;
  }
  @media only screen and (max-width: 497px) {
    aside{
        display: none;
    }
    .right-section{
        display: none;
    }
    .on{
        display: block;
    }
  }
  @media only screen and (min-width: 497px) {
    .display-none{
        display: none;
    }
  }